import { useEffect, useRef, useState } from "react";

export function useSlideInWhenScroll(rootMargin = "50px 0px 50px 0px") {
    const [node, setNode] = useState(null);
    const [entry, updateEntry] = useState({});
    const callback = ([entry]) => {
        // Get intersection data here
        // Like entry.intersectionRatio
        // Here we can set state or update ref
        // based on entry data
        updateEntry(entry)
    };
    const observer = useRef(new window.IntersectionObserver(callback, {
        root: null,
        rootMargin,
        // Creates a threshold of with increments of 0.01
        threshold: new Array(101).fill(0).map((v, i) => i * 0.01),
    }))
    useEffect(() => {
        observer.current.disconnect()
        if (node) {
            observer.current.observe(node)
        }
        return () => observer.current.disconnect()
    }, [node])
    useEffect(() => {
        let itemPercent = 100 * entry.intersectionRatio
        // scroll down won't disappear
        if (entry.boundingClientRect && entry.boundingClientRect.top < entry.intersectionRect.top) {
            itemPercent = 100
        }
        if (node) {
            node.style['transform'] = `translateX(${(100 - itemPercent)}px)`
            node.style['opacity'] = itemPercent / 100
        }
    }, [node, entry])

    return [setNode]
}